<template>
  <div class="page-index" style="text-align: center; cursor: pointer;">
    <!-- <index-college></index-college> -->
    <div v-if="roundOne" @click="start1" style="margin-bottom: 20px; width: 100px; height: 100px; background: yellow; line-height: 100px; text-align: center; cursor">開始第一回</div>
    <div v-if="roundTwo" @click="start2" style="margin-bottom: 20px; width: 100px; height: 100px; background: yellow; line-height: 100px; text-align: center; cursor">開始第二回</div>
    <div v-if="roundThree" @click="start3" style="margin-bottom: 20px; width: 100px; height: 100px; background: yellow; line-height: 100px; text-align: center; cursor">開始第三回</div>
    <div style="color: blue;">本局：{{ win }} 勝 {{ lose }} 敗</div>
    <div>目前是第 {{ round }} 回合</div>
    <div>倒數 <span style="color: red;">{{ second }}</span> 秒</div>
    <div style="margin-top: 30px;">林開群總怪力值：{{ monsterStrengh }}</div>
    <el-progress :percentage="monsterPercentage"></el-progress>
    <el-progress :percentage="myPercentage"></el-progress>
    <div>我的總怪力值：{{ myStrengh }}</div>
    <div v-if="isShowFight" @click="fight" style=" color: white; height: 80px; background: red; line-height: 80px; text-align: center; border-radius: 100px; margin: 30px; auto 30px;">請點我</div>
    <div v-if="isShowResult" style="margin-top: 10px; color: pink;">{{result}}</div>
  </div>
</template>

<script>
// import IndexCollege from '@/pages/index/components/IndexCollege'

export default {
  name: 'PageIndex',
  components: {
    // IndexCollege
  },
  data () {
    return {
      roundOne: true,
      roundTwo: false,
      roundThree: false,
      monsterStrengh: 87,
      myStrengh: 100,
      win: 0,
      lose: 0,
      second: 10,
      round: 1,
      monsterPercentage: 0,
      myPercentage: 0,
      isShowFight: false
    }
  },
  mounted () {
  },
  methods: {
    start1 () {
      this.monsterPercentage = 0
      this.myPercentage = 0
      this.isShowFight = true
      this.roundOne = false
      let round1 = setInterval(() => {
        this.second--
      }, 1000)

      setTimeout(() => {
        this.monsterPercentage = 7
      }, 3000)

      setTimeout(() => {
        this.monsterPercentage = 0
      }, 4000)

      setTimeout(() => {
        this.monsterPercentage = 22
      }, 6000)

      setTimeout(() => {
        this.monsterPercentage = 0
      }, 7000)

      setTimeout(() => {
        this.monsterPercentage = 32
      }, 9000)

      setTimeout(() => {
        clearInterval(round1)
        this.roundTwo = true
        this.isShowFight = false
        this.round = 2
        this.monsterStrengh = this.monsterStrengh - this.monsterPercentage
        if (this.monsterPercentage > this.myPercentage) {
          this.lose++
        } else {
          this.win++
        }
      }, 10000)
    },
    start2 () {
      this.isShowFight = true
      this.myPercentage = 0
      this.monsterPercentage = 0
      this.second = 10
      this.roundTwo = false
      let round2 = setInterval(() => {
        this.second--
      }, 1000)

      setTimeout(() => {
        this.monsterPercentage = 14
      }, 3000)

      setTimeout(() => {
        this.monsterPercentage = 0
      }, 4000)

      setTimeout(() => {
        this.monsterPercentage = 20
      }, 6000)

      setTimeout(() => {
        this.monsterPercentage = 0
      }, 7000)

      setTimeout(() => {
        this.monsterPercentage = 23
      }, 9000)

      setTimeout(() => {
        clearInterval(round2)
        this.roundThree = true
        this.isShowFight = false
        this.round = 3
        this.monsterStrengh = this.monsterStrengh - this.monsterPercentage
        if (this.monsterPercentage > this.myPercentage) {
          this.lose++
        } else {
          this.win++
        }
      }, 10000)
    },
    start3 () {
      this.isShowFight = true
      this.myPercentage = 0
      this.monsterPercentage = 0
      this.second = 10
      this.roundThree = false
      let round3 = setInterval(() => {
        this.second--
      }, 1000)

      setTimeout(() => {
        this.monsterPercentage = 11
      }, 3000)

      setTimeout(() => {
        this.monsterPercentage = 0
      }, 4000)

      setTimeout(() => {
        this.monsterPercentage = 16
      }, 6000)

      setTimeout(() => {
        this.monsterPercentage = 0
      }, 7000)

      setTimeout(() => {
        this.monsterPercentage = 32
      }, 9000)

      setTimeout(() => {
        clearInterval(round3)
        // this.roundThree = true
        this.isShowFight = false
        // this.round = 3
        this.isShowResult = true
        if (this.win > this.lose) {
          this.result = '恭喜你收服林開群啦！'
        } else {
          this.result = '你被林開群打敗了，趕緊回去補充怪力值！'
        }

        this.monsterStrengh = this.monsterStrengh - this.monsterPercentage
        if (this.monsterPercentage > this.myPercentage) {
          this.lose++
        } else {
          this.win++
        }
      }, 10000)
    },
    fight () {
      if (this.myStrengh === 0) {

      } else {
        this.myStrengh--
        this.myPercentage++
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.page-index {

}
</style>



